import React from "react"
import { Link, graphql } from "gatsby"
import Img from "gatsby-image"

import Layout from "../components/layout"
import SEO from "../components/seo"
import "../css/index.css"

const IndexPage = ({ data }) => {
  return (
    <Layout>
      <SEO title="Home" />
      <div className="container-box">
        <h2>
          Angle
          <span className="company-name">Tech</span>
        </h2>
        <div className="caption">
          <div className="tagline">
            <p>
              We will help you to learn and practice the current industrial
              technologies by building together. Innovative mind needs
              extraordinary talents to achieve mind blowing results.
              <br />
              Come, Lets create together.
            </p>
            <Link to="/about/">
              <button className="docs">Check Your Eligibilty</button>
            </Link>
          </div>
          <Img
            className="pair-programing"
            fluid={data.pairProgramming.childImageSharp.fluid}
          />
        </div>
      </div>
    </Layout>
  )
}

export default IndexPage

export const query = graphql`
  query {
    pairProgramming: file(relativePath: { eq: "pair_programing.png" }) {
      childImageSharp {
        fluid(maxWidth: 1500) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`
